<template>
  <div class="container">
    <div class="row">
      <div class="col col-md-4 "></div>

      <!--核心组件 st-->
      <div class="col col-md-4 ">

        <!--图标 st-->
        <div class="text-center" style="padding: 32px 0 24px 0">
          <img src="@/assets/structure/login.png" style="height: 72px" alt="">
        </div>
        <!--图标 ed-->

        <!--欢迎语 st-->
        <div class="text-center">
          <p style="font-size: 24px">登录 upuper</p>

        </div>
        <!--欢迎语 ed-->

        <!--登录 st-->
        <div style="margin-top: 16px; border:1px solid #d8dee2 ; padding: 20px 20px 20px 20px">

          <!--表单 st-->
          <form @submit.prevent>

            <!--手机/用户名 st-->
            <div class="form-group">
              <label class="login-text">手机/用户名：</label>
              <input type="text" class="form-control" v-model="userName">
            </div>
            <!--手机/用户名 ed-->

            <!--密码 st-->
            <div class="form-group">
              <div class="row">
                <div class="col-md-4">
                  <label class="login-text">密码：</label>
                </div>
                <div class="col-md-4 offset-md-4">
                  <router-link :to="{ name: 'homepage', params: { }}">
                    <button type="button" class="btn btn-link login-text" style="padding-top: 0;">忘记密码
                    </button>
                  </router-link>
                </div>

              </div>

              <input type="password" class="form-control" v-model="userPassword">
            </div>
            <!--密码 ed-->

            <!--记住我 st-->
            <div class="form-group form-check">
              <input type="checkbox" class="form-check-input" v-model="rememberMe">
              <label class="form-check-label login-text">记住我</label>
            </div>
            <!--记住我 ed-->

            <!--登录按钮-->
            <button class="btn btn-primary login-text" @click="userLogin()">登录</button>
          </form>
          <!--表单 ed-->
        </div>
        <!--登录 ed-->

        <!--新用户 st-->
        <div style="margin: 16px 0 10px 0;border: 1px solid #d8dee2;padding: 15px 20px 15px 20px ">

          <div class="row">

            <div class="col-md-4">
              <label class="login-text" style="margin: 7px 0 0 0">新用户？</label>
            </div>

            <div class="col-md-4 offset-md-4">
              <router-link :to="{ name: 'register', params: { }}">
                <button class="btn btn-link login-text">立即注册</button>
              </router-link>

            </div>

          </div>

        </div>
        <!--新用户 ed-->

      </div>
      <!--核心组件 ed-->

    </div>
  </div>
</template>

<script>/* eslint-disable */
import axios from 'axios'

export default {
  data() {
    return {
      userName: '', //用户名
      userPassword: '', //用户密码
      rememberMe: false, //记住用户登录状态
      oreo: '', //cookie自动登陆获取账号密码

      //登陆之后获取用户电话、会员情况
      userTel: '',
      membership: -1,
    }
  },
  components: {},
  methods: {
    /*
    * 用户登录
    */
    userLogin() {
      //console.log(this.userName);
      //console.log(this.userPassword);
      //console.log(this.rememberMe);
      axios({
        method: 'post',
        url: 'http://schoolmock.com',
        data: {
          'username': this.username,
          'password': this.userPassword,
        }
      }).then((res) => {

        //console.log(res.data.data.userTel);
        this.userTel = res.data.data.userTel;
        this.membership = res.data.data.membership;
        //console.log(this.userTel);

        //登陆成功 页面跳转至主页
        this.jumpPage();
        //登陆成功 根据用户选择 记住登录状态
        this.rememberUser();
      });
    },

    /*
    *1、页面跳转
    * */
    jumpPage() {
      this.$router.push({
        path: '/info',
        name: 'info',
        params: {
          userName: this.userName,
          userTel: this.userTel,
          membership: this.membership,
        }
      })
      ;
    },

    /*
    * 2、记住登录状态
    * */
    rememberUser() {
      if (this.rememberMe) {
        console.log("保存登录状态.....")
        var date = new Date();
        var ms = 30 * 24 * 3600 * 1000;
        date.setTime(date.getTime() + ms);
        document.cookie = "user= userName{{{" + this.userName + "}}}" + "userPassword=[[[" + this.userPassword + "]]]; expires=" + date.toGMTString();
      }
    },
    /*
        * 用户登录
        */

    /*
    * Cookie---用户自动登陆
    * */
    autoLogin() {
      this.oreo = document.cookie;
      if (this.oreo === "") {
        console.log("无法自动登录");
      } else {
        let nameBegin = document.cookie.indexOf("{{{") + 3;
        let nameEnd = document.cookie.indexOf("}}}");
        let userName = document.cookie.slice(nameBegin, nameEnd);
        let passwordBegin = document.cookie.indexOf("[[[") + 3;
        let passwordEnd = document.cookie.indexOf("]]]");
        let userPassword = document.cookie.slice(passwordBegin, passwordEnd);
        console.log(userName);
        console.log(userPassword);
        axios({
          method: 'post',
          url: 'http://schoolmock.com',
          data: {
            'username': userName,
            'password': userPassword,
          }
        }).then((res) => {
          let userTel = res.data.data.userTel;
          let membership = res.data.data.membership;
          //登陆成功 页面跳转至主页
          this.$router.push({
            path: '/info',
            name: 'info',
            params: {
              userName: userName,
              userTel: userTel,
              membership: membership,
            }
          })

          //console.log(this.oreo)
        })
      }
    },

    /*
    * Cookie---用户自动登陆
    * */
  },

  mounted() {
    this.autoLogin();
  }

}
</script>

<style>
  .login-text {
    font-size: 14px;
  }
</style>



